html {
  box-sizing: border-box;
  font-size: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit;
  font-family: Poppins; }

/*--------------------------------------------------------------
## Reset
--------------------------------------------------------------*/
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, form, fieldset, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0; }

html {
  -webkit-font-smoothing: antialiased; }

h1, h2, h3, h4, h5, h6 {
  font-weight: normal; }

article, aside, details, figcaption, figure, footer, header, hgroup, nav, section {
  display: block; }

audio, canvas, video {
  display: inline-block;
  *display: inline;
  *zoom: 1; }

audio:not([controls]) {
  display: none; }

[hidden] {
  display: none; }

/*--------------------------------------------------------------
## Base Reset
--------------------------------------------------------------*/
html {
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

html, button, input, select, textarea {
  font-family: sans-serif;
  color: #222; }

body {
  margin: 0;
  font-size: 1em;
  line-height: 1.4;
  -moz-osx-font-smoothing: grayscale; }

::-moz-selection {
  background: #000;
  color: #fff;
  text-shadow: none; }

::selection {
  background: #000;
  color: #fff;
  text-shadow: none; }

/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
  text-decoration: none;
  color: #000; }

a:focus, a:hover, a:active {
  outline: none; }

/*--------------------------------------------------------------
## Typography
--------------------------------------------------------------*/
abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: bold; }

blockquote {
  margin: 0; }

dfn {
  font-style: italic; }

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0; }

ins {
  background: #ff9;
  color: #000;
  text-decoration: none; }

pre, code, kbd, samp {
  font-family: monospace, serif;
  _font-family: 'courier new', monospace;
  font-size: 1em; }

pre {
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: none; }

q::before, q::after {
  content: "";
  content: none; }

small {
  font-size: 85%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

/*--------------------------------------------------------------
## Lists
--------------------------------------------------------------*/
nav ul,
nav ol,
ul,
li {
  list-style: none;
  list-style-image: none;
  margin: 0;
  padding: 0; }

/*--------------------------------------------------------------
## Embedded Content
--------------------------------------------------------------*/
img {
  border: 0;
  -ms-interpolation-mode: bicubic;
  vertical-align: middle; }

svg:not(:root) {
  overflow: hidden; }

/*--------------------------------------------------------------
## Forms
--------------------------------------------------------------*/
figure {
  margin: 0; }

form {
  margin: 0; }

fieldset {
  border: 0;
  margin: 0;
  padding: 0; }

label {
  cursor: pointer; }

legend {
  border: 0;
  *margin-left: -7px;
  padding: 0;
  white-space: normal; }

button, input, select, textarea {
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
  *vertical-align: middle; }

button, input {
  line-height: normal; }

button, input[type="button"], input[type="reset"], input[type="submit"] {
  cursor: pointer;
  -webkit-appearance: button;
  *overflow: visible; }

button[disabled], input[disabled] {
  cursor: default; }

input, textarea {
  -webkit-box-shadow: none; }

input[type="checkbox"], input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
  *width: 13px;
  *height: 13px; }

input[type="search"] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

input[type="search"]::-webkit-search-decoration, input[type="search"]::-webkit-search-cancel-button {
  -webkit-appearance: none; }

button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical; }

input:invalid, textarea:invalid {
  background-color: #f0dddd; }

input, textarea, select {
  outline: none; }

/*--------------------------------------------------------------
## Tables
--------------------------------------------------------------*/
table {
  border-collapse: collapse;
  border-spacing: 0; }

td {
  vertical-align: top; }

/*--------------------------------------------------------------
## Chrome Frame Prompt
--------------------------------------------------------------*/
.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: black;
  padding: 0.2em 0; }

.underline {
  position: relative; }
  .underline::after {
    content: "";
    position: absolute;
    top: calc(100% + 25px);
    height: 2px;
    width: 40px;
    background-color: #af943e; }
  .underline.left::after {
    left: 0; }
  .underline.center::after {
    left: 50%;
    transform: translateX(-50%); }

.site-header {
  /*
  ** Quick contacts section 
  */
  /*
  ** Main header section 
  */
  /*
  ** Hamburger 
  */ }
  .site-header__quick-contacts {
    background-color: #2e2e2f;
    color: #fff;
    display: flex;
    justify-content: space-between;
    padding: 14px 0;
    margin: 0 auto;
    padding-left: 1em;
    padding-right: 1em; }
  .site-header__reachout {
    display: flex;
    flex-direction: column; }
  .site-header__social {
    display: flex;
    flex-direction: column; }
  .site-header__main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    padding: 22px 0;
    padding-left: 1em;
    padding-right: 1em; }
  .site-header__hamburger {
    padding: 10px 0;
    cursor: pointer; }
    .site-header__hamburger .hamburger {
      position: relative;
      height: 20px;
      width: 35px; }
      .site-header__hamburger .hamburger__line {
        position: absolute;
        width: 35px;
        height: 4px;
        background-color: #666666;
        transform-origin: center;
        transition: transform 300ms; }
      .site-header__hamburger .hamburger .l1 {
        top: 0; }
      .site-header__hamburger .hamburger .l2 {
        top: 8px; }
      .site-header__hamburger .hamburger .l3 {
        top: 16px; }
    .site-header__hamburger.on .l1 {
      transform: translateY(8px) rotate(40deg); }
    .site-header__hamburger.on .l2 {
      transform: translateX(100px); }
    .site-header__hamburger.on .l3 {
      transform: translateY(-8px) rotate(-40deg); }
  .site-header__nav {
    display: none; }
    .site-header__nav .site-nav__list {
      display: flex;
      justify-content: space-between;
      font-size: 18px; }

.site-content {
  height: 100px;
  width: 100%;
  background-color: #5ed9e7; }

.site-footer {
  display: flex;
  flex-direction: column;
  background-color: #181a1b;
  color: #888888;
  /*
  ** CONTENT
  */
  /*
  ** Social links
  */
  /*
  ** Footer headers
  */
  /*
  ** More links
  */
  /*
  ** Our address
  */
  /*
  ** Copyrights
  */ }
  .site-footer__content {
    padding: 40px 0;
    padding-left: 1em;
    padding-right: 1em;
    display: flex;
    flex-direction: column; }
    .site-footer__content > div {
      margin-bottom: 20px; }
    .site-footer__content div:last-child {
      margin-bottom: 0; }
  .site-footer__social .site-footer__logo-wrapper,
  .site-footer__social p {
    margin-bottom: 28px; }
  .site-footer__social-icons {
    display: flex;
    justify-content: center; }
  .site-footer__social-icon-wrapper {
    position: relative;
    height: 35px;
    width: 35px;
    border-radius: 50%;
    border: 1px solid #af943e;
    margin-right: 10px; }
    .site-footer__social-icon-wrapper img {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%); }
  .site-footer__header {
    color: #fff;
    font-size: 18px;
    text-transform: uppercase;
    margin-bottom: 54px; }
  .site-footer__links ul {
    padding-left: 35px; }
  .site-footer__links a {
    color: #888888;
    transition: color 200ms; }
    .site-footer__links a:hover {
      color: #fff; }
      .site-footer__links a:hover li::before {
        transform: translateX(10px); }
  .site-footer__links li {
    position: relative;
    margin-bottom: 1em; }
    .site-footer__links li::before {
      position: absolute;
      content: ">>";
      right: calc(100% + 20px);
      color: #fff;
      transition: transform 200ms; }
  .site-footer__address ul {
    padding-left: 35px; }
  .site-footer__address li {
    position: relative;
    margin-bottom: 1em;
    transition: color 200ms; }
    .site-footer__address li::before {
      position: absolute;
      content: "";
      right: calc(100% + 20px);
      top: 50%;
      transform: translateY(-50%);
      width: 13px;
      height: 12px;
      background-position: center;
      background-repeat: no-repeat; }
    .site-footer__address li.address__house::before {
      background-image: url(./images/house.png); }
    .site-footer__address li.address__phone::before {
      background-image: url(./images/phone.png); }
    .site-footer__address li:hover {
      color: #fff; }
  .site-footer__copyrights {
    padding: 18px 0;
    padding-left: 1em;
    padding-right: 1em;
    background-color: #141516;
    text-align: center; }
